<template><section><h1>dialog</h1>
<p>更为复杂的弹出框</p>
<template>
	<coding :code="datas" title="dialog" content="跟modal相似，但是扩展性更强，适用于稍复杂的。可以使用v-model绑定来自行控制dialog的弹出消失。">
		<y-button @click.native="test = true">啦啦啦</y-button>
	</coding>
	<y-dialog v-model="test" title="hello world">
		<template slot="content">
			<input v-model="input1">
		</template>
		<template slot="footer">
            <y-button type="ghost" @click.native="test = false">取消</y-button>
            <y-button type="primary" @click.native="close1">确认</y-button>
		</template>
	</y-dialog>
</template>

<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>slot::content</td>
<td>弹出框内容</td>
<td><code>slot</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>slot::footer</td>
<td>底部内容</td>
<td><code>slot</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>title</td>
<td>标题</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
	data(){
		return {
			test:false,
			input1:"",
datas:
`<y-button @click.native="test = true">啦啦啦</y-button>
<y-dialog 
	v-model="test"
	title="hello world"
>
	<template slot="content">
		<input v-model="input1">
	</template>
	<template slot="footer">
        <y-button type="ghost" @click.native="test = false">取消</y-button>
        <y-button type="primary" @click.native="close1">确认</y-button>
	</template>
</y-dialog>
`
		}
	},
	methods:{
		close1(){
			this.$notify(`您输入的是${this.input1}`)
			this.test = false
		}
	}
}
</script>


